'use client'

import { useState, useEffect } from 'react'
import { rechercherHorairesAvecObjet } from '../actions/horairesActions';

interface Schedule {
  departure_time: string
  arrival_time: string
  duration: string
  transfers: number
}

interface ScheduleResultsProps {
  city: string
  line: {
    number: string
    terminus_a: string
    terminus_b: string
  }
  journeyDetails: {
    departure: string        // NOM de la gare (ex: "Agadir Centre Ville")
    arrival: string          // NOM de la gare (ex: "Talborjt")
    date: string            // Format: "2024-11-25"
  }
  onNewSearch: () => void
}

// Fonction pour rechercher les IDs des gares
async function rechercherIdsGares(ligneId: string, depart: string, arrivee: string) {
  try {
    const response = await fetch('/api/rechercher-ids-gares', {
      method: 'POST',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify({ 
        ligne_id: ligneId, 
        depart, 
        arrivee 
      })
    });
    
    if (!response.ok) {
      const errorData = await response.json();
      throw new Error(errorData.detail || 'Erreur lors de la recherche des IDs');
    }
    
    return await response.json();
  } catch (error) {
    console.error('Erreur recherche IDs:', error);
    throw error;
  }
}

export default function ScheduleResults({
  city,
  line,
  journeyDetails,
  onNewSearch
}: ScheduleResultsProps) {
  const [schedules, setSchedules] = useState<Schedule[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [garesInfo, setGaresInfo] = useState<{
    departureName: string
    arrivalName: string
  }>({
    departureName: journeyDetails.departure,
    arrivalName: journeyDetails.arrival
  })

  useEffect(() => {
    const loadSchedules = async () => {
      setLoading(true)
      setError(null)
      
      try {
        // ⭐ ÉTAPE 1: Rechercher les IDs des gares via l'API
        const ids = await rechercherIdsGares(
          line.number, 
          journeyDetails.departure, 
          journeyDetails.arrival
        )

        // ⭐ ÉTAPE 2: Appeler l'API des horaires avec les IDs
        const result = await rechercherHorairesAvecObjet({
          ligne_id: line.number,                    // Ex: "L101"
          arret_depart_id: ids.depart_id,           // Ex: "AGD_ARR001" (ID de la BDD)
          arret_arrivee_id: ids.arrivee_id,         // Ex: "AGD_ARR002" (ID de la BDD)
          date_recherche: journeyDetails.date       // Ex: "2024-11-25"
        })

        if (result.success) {
          // Transformation des données API
          const horairesFormates = result.horaires.map(horaire => ({
            departure_time: horaire.heure_depart.substring(0, 5), // "08:15:00" → "08:15"
            arrival_time: horaire.heure_arrivee.substring(0, 5),
            duration: `${Math.floor(horaire.duree_minutes / 60)}h${(horaire.duree_minutes % 60).toString().padStart(2, '0')}min`,
            transfers: 0
          }))
          
          setSchedules(horairesFormates)
        } else {
          setError(result.error || 'Erreur lors de la récupération des horaires')
          setSchedules([])
        }
      } catch (err) {
        setError(err instanceof Error ? err.message : 'Erreur de connexion au serveur')
        setSchedules([])
      } finally {
        setLoading(false)
      }
    }

    loadSchedules()
  }, [line.number, journeyDetails.departure, journeyDetails.arrival, journeyDetails.date])

  const formatDate = (dateString: string) => {
    const date = new Date(dateString)
    return date.toLocaleDateString('fr-FR', {
      weekday: 'long',
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    })
  }

  if (loading) {
    return (
      <div className="ml-[280px] min-h-screen bg-white p-8">
        <div className="max-w-4xl mx-auto">
          <div className="flex justify-center items-center h-64">
            <div className="text-lg text-gray-600">Recherche des horaires...</div>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="ml-[280px] min-h-screen bg-white p-8">
      <div className="max-w-4xl mx-auto">
        {/* Header */}
        <div className="mb-8 flex items-center justify-between">
          <div>
            <h2 className="text-3xl font-bold text-[#1e40af] mb-2">
              Trajets disponibles — {formatDate(journeyDetails.date)}
            </h2>
            <p className="text-[#374151]">
              Parcours : {journeyDetails.departure} → {journeyDetails.arrival}
            </p>
            {error && (
              <p className="text-red-500 text-sm mt-2">
                {error}
              </p>
            )}
          </div>
          <button
            onClick={onNewSearch}
            className="px-6 py-2 text-[#1e40af] font-medium hover:bg-gray-100 rounded-lg transition-colors"
          >
            Nouvelle recherche
          </button>
        </div>

        {/* Schedules List */}
        <div className="space-y-4">
          {schedules.length === 0 && !error ? (
            <div className="text-center py-8 text-gray-500">
              Aucun horaire disponible pour cette date
            </div>
          ) : schedules.length === 0 && error ? (
            <div className="text-center py-8 text-red-500">
              {error}
            </div>
          ) : (
            schedules.map((schedule, index) => (
              <div
                key={index}
                className="border border-[#e5e7eb] rounded-lg p-6 hover:shadow-lg transition-shadow bg-white"
              >
                {/* Schedule Header */}
                <div className="flex flex-col md:flex-row md:items-center md:justify-between mb-6 gap-4">
                  {/* Departure */}
                  <div className="flex-1">
                    <p className="text-sm text-[#374151]">DÉPART</p>
                    <p className="text-2xl font-bold text-[#1e40af]">
                      {schedule.departure_time}
                    </p>
                  </div>

                  {/* Duration & Transfers */}
                  <div className="flex-1 text-center">
                    <p className="text-sm text-[#374151]">DURÉE</p>
                    <p className="text-2xl font-bold text-[#374151]">
                      {schedule.duration}
                    </p>
                    <p className="text-xs text-[#6b7280] mt-1">
                      {schedule.transfers > 0
                        ? `(${schedule.transfers} corresp.)`
                        : '(direct)'}
                    </p>
                  </div>

                  {/* Arrival */}
                  <div className="flex-1 text-right">
                    <p className="text-sm text-[#374151]">ARRIVÉE</p>
                    <p className="text-2xl font-bold text-[#1e40af]">
                      {schedule.arrival_time}
                    </p>
                  </div>
                </div>

                {/* Route Info */}
                <div className="flex flex-col md:flex-row md:items-center md:justify-between text-sm text-[#374151] mb-6 pb-6 border-b border-[#e5e7eb]">
                  <span>{journeyDetails.departure}</span>
                  <span className="hidden md:inline">━━━</span>
                  <span>{journeyDetails.arrival}</span>
                </div>

                {/* Reserve Button */}
                <div className="flex justify-center">
                  <button className="bg-[#1e40af] text-white px-8 py-3 rounded font-bold hover:bg-[#1e3a8a] transition-colors">
                    RÉSERVER CE TRAJET
                  </button>
                </div>
              </div>
            ))
          )}
        </div>
      </div>
    </div>
  )
}